  /* Выпадающий список */
}
.filter-dropdown__button {
  position: relative;
  display: flex;
  align-items: center;
  padding: 1rem;
  padding-right: 4rem;
  min-height: 4.8rem;
  border: 2px solid #e8e6ed;
  border-radius: 4px;
