  white-space: nowrap;
}
.filter-dropdown__button span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 0;
  height: 0;
  transform: rotateY(90deg);
  border-radius: 50%;
  color: #ffffff;
  background-color: #2c2b3c;
}
@media (max-width: 960px) {
  .filter-dropdown__button span {
    margin-left: auto;
  }
}
.filter-dropdown__button span.js-filter-selected {
  width: 2.4rem;
  height: 2.4rem;
  transform: rotateY(0);
  animation: rotateIn 0.7s linear forwards;
  margin-left: 0.8rem;
}
.filter-dropdown__button::after {
  position: absolute;
  top: 50%;
  right: 0.8rem;
  transform: translateY(-50%);
  content: "";
  width: 2.4rem;
  height: 2.4rem;
  background-image: url("../assets/img/dropdown-arr.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  transition: transform 0.4s 0.1s ease;
}
.filter-dropdown__button.active::after {
  transform: translateY(-50%) rotate(180deg);
}
@keyframes rotateIn {
  0% {
    width: 0;
    height: 0;
    transform: rotateY(90deg);
  }
  30% {
    width: 2.4rem;
    height: 2.4rem;
    transform: rotateY(90deg);
  }
  35% {
    transform: rotateY(90deg);
  }
  100% {
    transform: rotateY(0);
  }
}
@keyframes rotateOut {
  0% {
    width: 2.4rem;
    height: 2.4rem;
    transform: rotateY(0);
  }
  60% {
    width: 2.4rem;
    height: 2.4rem;
    transform: rotateY(90deg);
  }
  65% {
    width: 2.4rem;
    height: 2.4rem;
    transform: rotateY(90deg);
  }
  100% {
    width: 0;
    height: 0;
    transform: rotateY(90deg);
  }
}
